
<!-- 
    ,idid
-->
<template>
    <div style="width: 50%;">
        <header>
            <router-link to="/all"></router-link> >>> 
        </header>
        <el-form label-width="80px" :model="form" ref="form" class="demo-ruleForm my_inp">
            <el-form-item label="" >
                <el-input v-model="form.title" readonly="readonly"></el-input>
            </el-form-item>
              <el-form-item label="" >
                <el-input v-model="form.kind" readonly="readonly"></el-input>
            </el-form-item>

           <el-form-item label="">
                <!-- <el-input type="textarea" v-model="form.content" readonly="readonly" ></el-input> -->

                <!-- quill-editor -->
				<quill-editor v-model="form.content" ref="myQuillEditor" disabled :options="editorOption"></quill-editor>

            </el-form-item>

            <el-form-item label="" class="my_inp">
                <el-input type="text" v-model="form.writer" readonly="readonly"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="back()"></el-button> 
            </el-form-item>
        </el-form>
    </div>
   
</template>
<script>
import hljs from 'highlight.js'
import 'highlight.js/styles/xcode.css'
export default {
    name:"check",
    data(){
        return{
            queryId:'',  //id
            form:{},   //
            url:"http://127.0.0.1:3000",
            // quill-editor
			editorOption:{
				modules:{
					toolbar:[
						['bold', 'italic', 'underline', 'strike'],    //
						['blockquote', 'code-block'],     //
						[{ 'size': ['small', 'normal', 'large', 'huge'] }], // 
						[{ 'list': 'ordered'}, { 'list': 'bullet' }],     //
						[{ 'color': [] }, { 'background': [] }],     // 
						['image','video']    //
					],
					syntax:{
						highlight: text =>{
							return hljs.highlightAuto(text).value;
						} 
					}
				},
				
			},
        }
    },
    created(){
        this.getRoutId();
    },
    methods: {
        getRoutId(){
           this.queryId=this.$route.query.id;   //id
            //  id  
           this.$axios.get(this.url+"/details",{params:{
               id:this.queryId
           }})
           .then(result=>{
               this.form=result.data;
               console.log(result)
           })
           .catch(err=>{
               console.log(err)
           })
        },
        // 
        back(){
            this.$router.go(-1)
        },
    }
}
</script>
<style scoped>
    header{
        padding: 0 0 20px;
        font-size: 14px
    }
    header a{
        color: #409EFF
    }

    .el-textarea__inner{
	    resize: none;
    }
    .ql-container{
		height: 500px;
	}
    /deep/ .ql-container{
		height: 500px;
	}
	/deep/ .ql-editor img{
		width: 20%;
		height:20%;
	}
	/deep/ .my_inp input{
		width: 50%
	}
</style>


