



<!-- 
	1:  ,
	2:  

 -->
<template>
	<div style="width: 70%;">
		<el-form label-width="80px" :rules="rules" :model="form" ref="form" class="demo-ruleForm">
			<el-form-item label="" prop="title">
				<el-input v-model="form.title" class="my_inp">

				</el-input>
			</el-form-item>
			<el-form-item label="" prop="kind">
				<el-select v-model="form.kind" placeholder="">
				  <el-option label="PHP" value="PHP"></el-option>
				  <el-option label="Node.js" value="Node.js"></el-option>
				  <el-option label="h5+css3" value="h5+css3"></el-option>
				  <el-option label="Vue" value="Vue"></el-option>
				  <el-option label="Mongodb" value="Mongodb"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="" prop="content">
				
				<!-- quill-editor -->
				<quill-editor class="ql-editor" v-model="form.content" ref="myQuillEditor" :options="editorOption"></quill-editor>

					<!-- VueEditor -->
				<!-- <VueEditor v-model="form.content" :useCustomImageHandler="flag"  @imageAdded="handleImageAdded" :editorToolbar="customToolbar" placeholder=""></VueEditor> -->

			</el-form-item>
			<el-form-item label="" prop="writer" class="my_inp">
				<el-input type="text" placeholder=":" v-model="form.writer"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="onSure('form')"></el-button>
				<el-button @click="onReset('form')"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>

import { VueEditor } from 'vue2-editor'    //*vue2-editor
import quillConfig from "../../../assets/quill-config.js"
import hljs from 'highlight.js'
import 'highlight.js/styles/xcode.css'
import QS from 'qs'

export default({
	name:"add",
	data(){
		return{
			flag:true,
			url:"http://127.0.0.1:3000",
			form:{
				title:"",
				kind:"",
				content:"",
				writer:"",
			},
			rules:{
				title:[{
					required: true, message: '', trigger: 'blur'
				}],
				kind:[{
					required:true,message:"",trigger:'change'
				}],
				content:[{
					required:true,message:"",trigger:'blur'
				}]
			},
			
			// quill-editor
			editorOption:{
				
				modules:{
					toolbar:[
						['bold', 'italic', 'underline', 'strike'],    //
						['blockquote', 'code-block'],     //
						[{ 'indent': '-1'}, { 'indent': '+1' }],
						[{ 'size': ['small', 'normal', 'large', 'huge'] }], // 
						[{ 'list': 'ordered'}, { 'list': 'bullet' }],     //
						[{ 'color': [] }, { 'background': [] }],     // 
						// ['image']    //
					],
					syntax:{
						highlight: text =>{
							return hljs.highlightAuto(text).value;
						} 
					}
				},
				placeholder:"....."
			},

			// VueEditor
			// customToolbar:[
			// 	['bold', 'italic', 'underline','code-block','image'],
			// 	[{'list': 'ordered'}, {'list': 'bullet'}],
			// 	[{'indent': '-""'}, {'indent': '+""'}],
			// 	[{'header': '2'}], [{'align': 'center'}, {'align': 'justify'}, {'align': 'right'}],
				
			// ],
		}
	},
	// vueEditor
	components:{
    	VueEditor
	},
	methods:{
		handleImageAdded(file,Editor,cursorLocation){
			console.log(file,editor,cursorLocation)
			Editor.insertEmbed(cursorLocation, 'image', url);
		},

		
		// 
		onSure(form){
			this.$refs[form].validate((valid) => {
				if (valid){
					if(this.form.content){
						console.log(this.form.content);
						// ,1
						// this.$axios.post(this.url+"/getAdd",{
						// 	params:{
						// 		content:this.form.content,
						// 		title:this.form.title,
						// 		kind:this.form.kind,
						// 		writer:this.form.writer,
						// 		date:this.dateStr()
						// 	}
						// })
						this.$axios.post(this.url+"/getAdd",QS.stringify({
							content:this.form.content,
							content:this.form.content,
							title:this.form.title,
							kind:this.form.kind,
							writer:this.form.writer,
							date:this.dateStr()
						}))
						.then(res=>{
							console.log(res.data)
							// 
							if(res.data==1){
								this.$message({
									message:"",
									type:"success",
									durations:1500,
								})
								
								this.$refs[form].resetFields()   //
							}else{
								this.$message({
									message:"",
									type:"error",
									durations:1500,
								})
							}
						})
						.catch(err=>{
							this.$message({
								message:err.message,
								type:"error",
								durations:1500,
							})
						})
					} else {
						return false;
					}
				}
			})
		},

		// 
		onReset(form){
			this.$refs[form].resetFields()
		}
	}
})
</script>

<style scoped>
	.el-form-item__content{
		line-height: 30px;
	}
	/deep/ .ql-container{
		height: 500px;
	}
	
	.ql-editor{
		
		white-space: nowrap;
		padding: 0 0 !important;
	}
	
	.ql-editor img{
		width: 20%;
		height:20%;
	}
	.my_inp input{
		width: 50%
	}

	
</style>



